import * as React from 'react';
import Button from 'material-ui/Button';
import Dialog, {
    DialogActions,
    DialogContent,
    //DialogContentText,
    DialogTitle,
} from 'material-ui/Dialog';


type PassDialogProps = {
    open: boolean,
    onRequestClose: (pass: boolean) => void,
};

type State = {
    open: boolean;
};
export default class PassDialog extends React.Component<PassDialogProps, State> {
    state = {
        open: false,
    };

    handleClickOpen = () => {
        this.setState({ open: true });
    };
    handleRequestOk = () => {
        this.handleRequestClose(true);
    };
    handleRequestCancel = () => {
        this.handleRequestClose(false);
    };
    handleRequestClose = (pass = false) => {
        this.props.onRequestClose(pass);
    };

    render() {
        return (
            <Dialog open={this.props.open} onRequestClose={this.handleRequestCancel}>
                <DialogTitle>{"Are You Sure to Pass?"}</DialogTitle>
                <DialogContent>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleRequestCancel} color="primary" autoFocus>
                        CANCEL
                    </Button>
                    <Button onClick={this.handleRequestOk} color="primary" autoFocus>
                        OK
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }
}


