import * as React from 'react';

import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';
import Dialog, {
    DialogActions,
    DialogContent,
    DialogTitle,
} from 'material-ui/Dialog';

type ResulttDialogProps = {
    open: boolean,
    content: string,
    onRequestClose: () => void,
};



export default class ResulttDialog extends React.Component<ResulttDialogProps, any> {
    state = {
        open: false,
    };

    handleClickOpen = () => {
        this.setState({ open: true });
    };

    handleRequestClose = () => {
        this.props.onRequestClose();
        //this.setState({ open: false });
    };
    render() {
        return (
            <Dialog open={this.props.open} onRequestClose={this.handleRequestClose}>
                <DialogTitle>{"Final Score"}</DialogTitle>
                <DialogContent>
                    <Typography type="title" noWrap>
                        {this.props.content}
                    </Typography>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleRequestClose} color="primary">
                        OK
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }
}


