export const CLEAR = 0;
/** Black stone */
export const BLACK = 1;
/** White stone */
export const WHITE = 2;
/** Semi-transparent black stone */
export const DIM_BLACK = 3;
/** Semi-transparent white stone */
export const DIM_WHITE = 4;


// Alias all objects into globals
