/**
 * Enum for marker types.
 * @readonly
 * @enum
 */
export const MARK = {
    /** No marker ('') */
    NONE: '',
    /** Selected intersection */
    SELECTED: '^',
    /** Square */
    SQUARE: '#',
    /** Triangle */
    TRIANGLE: '/',
    /** Circle */
    CIRCLE: '0',
    /** Cross */
    CROSS: '*',
    /** Black territory */
    BLACK_TERRITORY: '-',
    /** White territory */
    WHITE_TERRITORY: '+'
};

/**
 * Board coordinate array.
 * @constant
 */
export const COORDINATES = 'ABCDEFGHJKLMNOPQRSTUVWXYZ'.split('');



