import * as React from 'react';
import Button from 'material-ui/Button';
import Dialog, {
    DialogActions,
    DialogContent,
    //DialogContentText,
    DialogTitle,
} from 'material-ui/Dialog';


type CPUPassDialogProps = {
    open: boolean,
    onRequestClose: () => void,
};

type State = {
    open: boolean;
};
export default class CPUPassDialog extends React.Component<CPUPassDialogProps, State> {
    state = {
        open: false,
    };
    handleClickOpen = () => {
        this.setState({ open: true });
    };
    handleRequestClose = () => {
        this.props.onRequestClose();
    };

    render() {
        return (
            <Dialog open={this.props.open} onRequestClose={this.handleRequestClose}>
                <DialogTitle>{"CPU Passed"}</DialogTitle>
                <DialogContent>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleRequestClose} color="primary" autoFocus>
                        OK
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }
}


