import * as React from 'react';
import { withStyles, WithStyles, Theme, StyleRules } from 'material-ui/styles';

import { Setup } from '../lib/JGO/setup';
import { RecordNode } from '../lib/JGO/node';
import { Record } from '../lib/JGO/record';
import { large } from '../lib/JGO/large/board';

import { WHITE, BLACK, CLEAR, MARK, DIM_WHITE, DIM_BLACK } from '../lib/JGO/constants';
import { Coordinate } from '../lib/JGO/coordinate';

import Button from 'material-ui/Button';
import Tooltip from 'material-ui/Tooltip';

import Typography from 'material-ui/Typography';

import PassDialog from './PassDialog';
import CPUPassDialog from './CPUPassDialog';
import ResulttDialog from './ResultDialog';
import DialogSelect from './DialogSelect';

import * as Commands from '../lib/Commands';

import Flag from 'material-ui-icons/Flag';
import BrightnessHigh from 'material-ui-icons/BrightnessHigh';
import PanTool from 'material-ui-icons/PanTool';

const styleClasses = {
    root: '',
    board: '',
    fab: '',
    row: '',
    content: '',
    sidebar: '',
    button: '',
    tooltip: '',
    info: '',
    stone: '',
};


const styles = (theme: Theme): StyleRules<any> => ({
    root: {
        // marginTop: theme.spacing.unit * 3,
        width: '100%',
        display: 'flex',
    },
    flex: {
        flex: 1,
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
    board: {
        width: '90vh',
    },
    button: {
        margin: theme.spacing.unit,
    },
    sidebar: {
        border: '1px solid rgba(200,200,200,0.5)',
    },
    tooltip: {
        display: 'flex',
    },
    row: {
        display: 'flex',
        alignItems: 'center',
    },
    info: {
        border: '1px solid rgba(200,200,200,0.5)',
    },
    stone: {
        margin: theme.spacing.unit,

    },
});

type State = {
    open: boolean,
    selectedValue: string,
    playerName1: string,
    playerName2: string,
    blackCaptures: number,
    whiteCaptures: number,
    openResultDialog: boolean,
    openPassDialog: boolean,
    openCPUPassDialog: boolean,
    gameOn: boolean,
    result: string,
    passDialogContent: string,
};

//function defaultGetWidth() {
//    return window.innerWidth;
//}

//function defaultGetHeight() {
//    return window.innerHeight;
//}

let player = BLACK;

let moveNum = 0;
let moves = 0;
let gotoMove = 0;
//var jnotifier;
let lastHover = false;
let turn = BLACK; // next player
let lastX = -1;
let lastY = -1; // hover helper vars
let ko: Coordinate = undefined;
let lastMove: Coordinate; // ko coordinate and last move coordinate
let jrecord = new Record(19);
let jboard = jrecord.jboard;

export function initializeValues(playerColor: number) {
    moveNum = 0;
    moves = 0;
    gotoMove = 0;
    lastHover = false;
    turn = BLACK; // next player
    lastX = -1;
    lastY = -1; // hover helper vars
    ko = undefined;
    lastMove = undefined;

    player = playerColor;
}


class GoBoard extends React.Component<WithStyles<keyof typeof styleClasses>, State> {
    private audio;
    state = {
        open: false,
        selectedValue: "",
        playerName1: 'Player',
        playerName2: 'CPU',
        blackCaptures: 0,
        whiteCaptures: 0,
        openResultDialog: false,
        openPassDialog: false,
        openCPUPassDialog: false,
        gameOn: true,
        result: "",
        passDialogContent: "",
    };

    handleRequestClose = () => {
        this.setState({
            open: false,
        });
    };

    handleRequestCloseResultDialog = () => {
        this.setState({
            openResultDialog: false,
            gameOn: false
        });
    };


    handleChangeSettings = (settings: { color: string, size: number }) => {
        let playerName1 = 'Player';
        let playerName2 = 'CPU';
        let playerColor = BLACK;
        if (settings.color === 'white') {
            playerName1 = 'CPU';
            playerName2 = 'Player';
            playerColor = WHITE;
        }

        let board = document.getElementById("board");
        board.removeChild(board.childNodes[0]);


        this.setBoardSize(settings.size);
        initializeValues(playerColor);


        this.setState({
            open: false,
            playerName1: playerName1,
            playerName2: playerName2,
            gameOn: true,
        });

        if (playerColor === WHITE) {
            let lastGenMove = Commands.genmove(turn);
            if (lastGenMove.pos === "PASS") {
                this.handleRequestOpenCPUPassDialog();
                return;
            }
            this.click(jboard.getCoordinate(lastGenMove.pos), undefined);
        }
    };

    handleClick = () => {
        this.setState({
            open: true,
        });
    };
    handleOpenResultDialog = () => {
        let score = Commands.finalScore();
        let result = score.color + " Win!" + "+" + score.point;

        this.setState({
            openResultDialog: true,
            result: result
        });
    };
    playerPass = () => {
        this.handleOpenPassDialog("Player Passed");
    };
    handleOpenPassDialog = (content: string) => {
        this.setState({
            passDialogContent: content,
            openPassDialog: true,
        });
    };
    handleRequestClosePassDialog = (pass: boolean) => {
        this.setState({
            openPassDialog: false,
        });
        if (!pass) {
            return;
        }

        turn = (turn === BLACK) ? WHITE : BLACK;

        let lastGenMove = Commands.genmove(turn);
        if (!this.state.gameOn) {
            return;
        }
        if (lastGenMove.pos === "PASS") {
            this.handleOpenResultDialog();
            return;
        }
        this.click(jboard.getCoordinate(lastGenMove.pos), undefined);


    };
    handleRequestOpenCPUPassDialog = () => {
        this.setState({
            openCPUPassDialog: true,
        });
    };
    handleRequestCloseCPUPassDialog = () => {
        this.setState({
            openCPUPassDialog: false,
        });
        turn = (turn === BLACK) ? WHITE : BLACK;
    };
    updateCaptures(node: RecordNode) {
        this.setState({
            blackCaptures: node.info.captures[BLACK],
            whiteCaptures: node.info.captures[WHITE]
        });
    }
    clearBoard() {
        Commands.gets("clear_board");
        jboard.clear();
    }
    setBoardSize(size: number) {
        this.clearBoard();
        Commands.gets("boardsize " + size);
        jrecord = new Record(size);
        jboard = jrecord.jboard;
        this.initialize();
    }
    soundPlay() {
    }

    click(coord: Coordinate, ev: KeyboardEvent) {
        //if (c.i < 10) move(-1); // back
        //if (c.i > 10) move(1); // forward

        var opponent = (turn == BLACK) ? WHITE : BLACK;

        //if (ev.shiftKey) { // on shift do edit
        //    if (jboard.getMark(coord) == JGO.MARK.NONE) {
        //        jboard.setMark(coord, JGO.MARK.SELECTED);
        //    } else {
        //        jboard.setMark(coord, JGO.MARK.NONE);
        //    }
        //    return;
        //}

        // clear hover away - it'll be replaced or then it will be an illegal move
        // in any case so no need to worry about putting it back afterwards
        if (lastHover) {
            jboard.setType(new Coordinate(lastX, lastY), CLEAR);
        }

        lastHover = false;

        var play = jboard.playMove(coord, turn, ko);

        if (play.success) {
            this.audio.play();

            let node = jrecord.createNode(true);
            node.info.captures[turn] += play.captures.length; // tally captures
            node.setType(coord, turn); // play stone
            node.setType(play.captures, CLEAR); // clear opponent's stones

            if (lastMove) {
                node.setMark(lastMove, MARK.NONE); // clear previous mark
            }
            if (ko) {
                node.setMark(ko, MARK.NONE); // clear previous ko mark
            }

            node.setMark(coord, MARK.CIRCLE); // mark move
            lastMove = coord;

            if (play.ko) {
                node.setMark(play.ko, MARK.CIRCLE); // mark ko, too
            }
            ko = play.ko;

            turn = opponent;
            this.updateCaptures(node);
            return true;
        } else {
            alert('Illegal move: ' + play.errorMsg);
        }
        return false;
    }
    initialize() {
        let jsetup = new Setup(jboard, large);
        jsetup.create('board', (canvas) => {
            canvas.addListener('click', (coord: Coordinate, ev: KeyboardEvent) => {
                if (coord.i == -1 || coord.j == -1 || coord.i >= jboard.width || coord.j >= jboard.width) {
                    return;
                }

                if (!this.state.gameOn) {
                    return;
                }
                if (player === turn) {
                    let s = jboard.toString(coord);

                    let result = this.click(coord, ev);
                    if (result) {
                        Commands.play(player, s);


                        let lastGenMove = Commands.genmove(turn);
                        if (lastGenMove.pos === "PASS") {
                            this.handleRequestOpenCPUPassDialog();
                            return;
                        }
                        this.click(jboard.getCoordinate(lastGenMove.pos), undefined);

                    }
                }
            });

            canvas.addListener('mousemove', (coord, ev) => {
                if (!this.state.gameOn) {
                    return;
                }

                if (coord.i == -1 || coord.j == -1 || (coord.i == lastX && coord.j == lastY)) {
                    return;
                }

                if (lastHover) // clear previous hover if there was one
                    jboard.setType(new Coordinate(lastX, lastY), CLEAR);

                lastX = coord.i;
                lastY = coord.j;

                if (jboard.getType(coord) == CLEAR && jboard.getMark(coord) == MARK.NONE) {
                    jboard.setType(coord, turn == WHITE ? DIM_WHITE : DIM_BLACK);
                    lastHover = true;
                } else
                    lastHover = false;
            });

            canvas.addListener('mouseout', (ev) => {
                if (!this.state.gameOn) {
                    return;
                }
                if (lastHover) {
                    jboard.setType(new Coordinate(lastX, lastY), CLEAR);
                }

                lastHover = false;
            });
        });
    }
    componentDidMount() {
        this.audio = document.getElementById('audio');
        this.initialize();
    }

    render() {
        const { classes } = this.props;
        return (
            <div className={classes.root}>
                <audio id="audio" src={'./sound/exe0000.wav'} ></audio>
                <div id="board" className={this.props.classes.board} ></div>
                <div className={classes.sidebar}>
                    <div className={classes.tooltip}>
                        <Tooltip id="tooltip-icon" title="Pass" placement="bottom">
                            <Button raised disabled={!this.state.gameOn} color="primary" className={classes.button} onClick={this.playerPass}>
                                <PanTool />
                            </Button>
                        </Tooltip>
                        <Tooltip id="tooltip-fab" className={classes.fab} title="Resign" placement="bottom">
                            <Button raised color="primary" disabled={!this.state.gameOn} className={classes.button} onClick={this.handleOpenResultDialog}>
                                <Flag />
                            </Button>
                        </Tooltip>
                        <Tooltip id="tooltip-fab" className={classes.fab} title="Settings" placement="bottom">
                            <Button raised color="primary" className={classes.button} onClick={this.handleClick}>
                                <BrightnessHigh />
                            </Button>
                        </Tooltip>
                    </div>
                    <div className={classes.info}>
                        <div className={classes.row}>
                            <img className={classes.stone} src={'./large/black.png'} />
                            <Typography type="headline">{this.state.playerName1}</Typography>
                        </div>
                        <Typography type="headline">Captures: {this.state.blackCaptures}</Typography>
                    </div>
                    <div className={classes.info}>
                        <div className={classes.row}>
                            <img className={classes.stone} src={'./large/white.png'} />
                            <Typography type="headline">{this.state.playerName2}</Typography>
                        </div>
                        <Typography type="headline">Captures: {this.state.whiteCaptures}</Typography>
                    </div>
                </div>
                <PassDialog open={this.state.openPassDialog} onRequestClose={this.handleRequestClosePassDialog} />
                <ResulttDialog open={this.state.openResultDialog} onRequestClose={this.handleRequestCloseResultDialog} content={this.state.result} />
                <DialogSelect open={this.state.open} onRequestClose={this.handleRequestClose} onRequestOk={this.handleChangeSettings} />
                <CPUPassDialog open={this.state.openCPUPassDialog} onRequestClose={this.handleRequestCloseCPUPassDialog} />
            </div >
        );
    }
}

export default withStyles(styles)<{}>(GoBoard);



