import * as React from 'react';
import Button from 'material-ui/Button';
import { withStyles, WithStyles, Theme, StyleRules } from 'material-ui/styles';
import Dialog, { DialogActions, DialogContent, DialogTitle } from 'material-ui/Dialog';
import Input, { InputLabel } from 'material-ui/Input';
import { MenuItem } from 'material-ui/Menu';
import { FormControl } from 'material-ui/Form';
import Select from 'material-ui/Select';

const styleClasses = {
    container: '',
    formControl: '',
    fab: '',
    drawerHeader: '',
    content: '',
    appFrame: '',
    button: '',
};

const styles = (theme: Theme): StyleRules<any> => ({
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    formControl: {
        margin: theme.spacing.unit,
        minWidth: 120,
    },
});

interface DialogSelectProps {
    open: boolean;
    onRequestClose: () => void;
    onRequestOk: (settings: { color: string, size: number }) => void;
};

interface State {
    open: boolean;
    age: string;
    color: string;
    size: number;
}


class DialogSelect extends React.Component<DialogSelectProps & WithStyles<keyof typeof styleClasses>, State> {
    state = {
        open: false,
        age: '',
        color: 'black',
        size: 19
    };

    handleChange = name => event => {
        this.setState({ [name]: Number(event.target.value) });
    };


    handleChangeColor = event => {
        this.setState({ color: event.target.value });
    };
    handleChangeSize = event => {
        this.setState({ size: Number(event.target.value) });
    };
    handleClickOpen = () => {
        this.setState({ open: true });
    };

    handleRequestClose = () => {
        //this.setState({ open: false });
        this.props.onRequestClose();
    };

    handleRequestOk = () => {
        //this.setState({ open: false });
        this.props.onRequestOk(this.state);
    };
    render() {
        const { classes } = this.props;

        return (
            <Dialog
                ignoreBackdropClick
                ignoreEscapeKeyUp
                open={this.props.open}
                onRequestClose={this.handleRequestClose}
            >
                <DialogTitle>Game Settings</DialogTitle>
                <DialogContent>
                    <form className={classes.container}>
                        <FormControl className={classes.formControl}>
                            <InputLabel htmlFor="age-native-simple">Your Color</InputLabel>
                            <Select
                                native
                                value={this.state.color}
                                onChange={this.handleChangeColor}
                                input={<Input id="age-native-simple" />}                            >
                                <option value={'black'}>Black</option>
                                <option value={'white'}>White</option>
                            </Select>
                        </FormControl>
                        <FormControl className={classes.formControl}>
                            <InputLabel htmlFor="age-simple">Board Size</InputLabel>
                            <Select
                                value={this.state.size}
                                onChange={this.handleChangeSize}
                                input={<Input id="age-simple" />}
                            >
                                <MenuItem value={9}>9×9</MenuItem>
                                <MenuItem value={19}>19×19</MenuItem>
                            </Select>
                        </FormControl>
                    </form>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.handleRequestClose} color="primary">
                        Cancel
                    </Button>
                    <Button onClick={this.handleRequestOk} color="primary">
                        Ok
                    </Button>
                </DialogActions>
            </Dialog>

        );
    }
}



export default withStyles(styles)<DialogSelectProps>(DialogSelect);


